body {
  color: #303841;
  font-family: Rubik,sans-serif;
  margin: 0;
}

.App {
  text-align: center;
}

.App::before {
  content: "";
  display: block;
  position: fixed;
  width: 300%;
  height: 100%;
  top: 50%;
  left: 50%;
  border-radius: 100%;
  transform: translateX(-50%) skewY(-8deg);
  background-color: #f6c90e;
  z-index: -1;
  text-align: center;
  animation: backgroudWave 8s ease-in-out infinite alternate;
}

@keyframes backgroudWave {
  0%   {top: 50%}
  50% {top: 65%}
}

